.l-main {
  &__support {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__support {
      display: flex;
      flex-direction: column;
    }
  }

  &__faq {
    background-position: center;
    background-size: cover;
    height: 680px;
    position: relative;
  }

  &__faq .pattern {
    background-color: rgba($color: #000, $alpha: 90%);
    height: 100%;
    position: absolute;
    width: 100%;
  }

  &__faq .inner {
    height: 100%;
    max-width: 560px;
    padding: 50px 0;
    position: absolute;
    right: 0;
    width: 100%;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__faq .inner {
      height: 100%;
      max-width: 100%;
      padding: 50px 0;
      position: relative;
      width: 100%;
    }
  }

  &__faq .inner h2 {
    color: var(--color-white);
    font-size: 40px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__faq .inner h2 {
      font-size: 30px;
      text-align: center;
    }
  }

  &__faq .faq-section {
    padding: 0 26px 0 0;
    width: 100%;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__faq .faq-section {
      padding: 0 16px;
      width: 100%;
    }
  }

  &__faq .faq-title {
    align-items: center;
    border: 1px solid var(--color-white);
    cursor: pointer;
    display: flex;
    height: 60px;
    justify-content: space-between;
    margin-bottom: 30px;
    width: 100%;
  }

  &__faq .faq-title h3 {
    color: var(--color-white);
    font-family: Poppins, sans-serif;
    font-size: 16px;
    margin: 0;
    padding: 0 16px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__faq .faq-title h3 {
      font-size: 14px;
      padding: 0 14px;
    }
  }

  &__faq .faq-title svg,
  img {
    filter: invert(59%) sepia(86%) saturate(350%) hue-rotate(2deg)
      brightness(95%) contrast(88%);
    margin-right: 16px;
    transition: var(--transition-duration-default);
    width: 14px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__faq .faq-title svg,
    img {
      height: 12px;
      width: 12px;
    }
  }

  &__faq p {
    border: 1px solid var(--color-white);
    border-top: 0;
    color: var(--color-light-gray);
    font-size: 14px;
    margin-top: -30px;
    padding: 20px;
    width: 100%;
  }

  &__faq .section-show p {
    display: block;
  }

  &__faq .section-hide p {
    display: none;
  }

  &__contact {
    height: 100%;
    width: 100%;
  }

  &__contact .inner {
    height: 100%;
    max-width: 560px;
    padding: 50px 0 50px 25px;
    width: 100%;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__contact .inner {
      height: 100%;
      max-width: 100%;
      padding: 50px 0;
      width: 100%;
    }
  }

  &__contact .inner h2 {
    font-size: 40px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__contact .inner h2 {
      font-size: 30px;
      text-align: center;
    }
  }

  &__form {
    position: relative;
    width: 100%;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__form {
      padding: 0 16px;
    }
  }

  &__form input {
    border: 1px solid var(--color-light-gray);
    font-size: 14px;
    height: 60px;
    margin-bottom: 30px;
    outline: none;
    padding: 0 14px;
    width: 100%;
  }

  &__form textarea {
    border: 1px solid var(--color-light-gray);
    font-size: 14px;
    height: 150px;
    outline: none;
    padding: 14px;
    width: 100%;
  }

  &__form button {
    background-color: var(--color-radiant);
    border: 0;
    bottom: 30px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    height: 40px;
    position: absolute;
    right: 20px;
    transition: var(--transition-duration-default);
    width: 130px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__form button {
      margin-top: 20px;
      position: initial;
      width: 100%;
    }
  }

  &__form button:hover {
    background-color: var(--color-pico-void);
    color: var(--color-white);
  }

  &__form .input-grid {
    display: flex;
    gap: 20px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__form .input-grid {
      flex-direction: column;
      gap: 0;
    }
  }
}
