.l-header {
  background-color: var(--color-pico-void);
  position: relative;
  width: 100%;
  z-index: 102;

  &__topbar {
    border-bottom: solid 1px var(--color-light-gray-border);
    height: 60px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__topbar {
      display: none;
    }
  }

  .topbar-inner {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: 0 auto;
    max-width: var(--container);
  }

  .topbar-email {
    font-size: 13px;
  }

  .topbar-email-link {
    align-items: center;
    color: var(--color-white);
    display: flex;
    gap: 10px;
    text-decoration: none;
  }

  .topbar-email-link img {
    width: 22px;
  }

  .topbar-appointment {
    font-size: 13px;
  }

  .topbar-appointment-link {
    align-items: center;
    border: 1px solid var(--color-white);
    color: var(--color-white);
    display: flex;
    font-weight: 400;
    height: 40px;
    justify-content: center;
    text-decoration: none;
    transition: var(--transition-duration-default);
    width: 170px;
  }

  .topbar-appointment-link:hover {
    background-color: var(--color-white);
    color: var(--color-pico-void);
  }

  &__navbar {
    height: 120px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__navbar {
      height: 100%;
      width: 100%;
    }
  }

  .navbar-inner {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: 0 auto;
    max-width: var(--container);
  }

  @media (min-width: 280px) and (max-width: 560px) {
    .navbar-inner {
      height: 60px;
      max-width: var(--container);
      padding: 60px 30px;
    }
  }

  .navbar-inner .navbar-inner-logo {
    display: flex;
    line-height: 0;
    margin: 0;
    padding: 0;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    .navbar-inner .navbar-inner-logo {
      width: 150px;
    }
  }

  @media (min-width: 280px) and (max-width: 560px) {
    .navbar-inner-nav {
      display: none;
    }
  }

  .navbar-inner-nav-mobile ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .btn-menu {
    display: none;
  }

  @media (max-width: 700px) {
    .menu-mobile {
      background-color: #151c2f;
      color: var(--color-white);
      display: block;
      height: 0;
      left: 0;
      overflow-y: hidden;
      position: absolute;
      text-align: center;
      top: 120px;
      transition: var(--transition-duration-default);
      visibility: hidden;
      width: 100%;
      z-index: 999999;
    }

    .menu-mobile a {
      align-items: center;
      border-bottom: 1px solid rgb(238 191 88 / 30%);
      color: var(--color-white);
      display: flex;
      justify-content: center;
      padding: 30px 0;
      text-decoration: none;
    }

    .btn-menu {
      align-items: center;
      background: none;
      border: 0;
      color: var(--color-white);
      cursor: pointer;
      display: flex;
      gap: 0.5rem;
      padding: 1rem 0;
    }

    .navbar-inner-nav-mobile.active .menu-mobile {
      height: calc(100vh - 120px);
      overflow-y: auto;
      visibility: visible;
    }

    .navbar-inner-nav-mobile .hamburger {
      border-top: 0.125rem solid;
      width: 1.25rem;
    }

    .hamburger::before,
    .hamburger::after {
      background: currentcolor;
      content: ' ';
      display: block;
      height: 0.125rem;
      margin-top: 0.3125rem;
      position: relative;
      transition: var(--transition-duration-default);
      width: 1.25rem;
    }
  }

  @media (min-width: 1200px) {
    .navbar-inner-nav-mobile {
      display: none;
    }
  }

  @media (min-width: 1800px) {
    .navbar-inner-nav-mobile {
      display: none;
    }
  }

  .navbar-inner-nav a {
    color: var(--color-white);
    display: inline-block;
    font-size: 14px;
    line-height: 120px;
    padding: 0 20px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    transition: var(--transition-duration-default);
  }

  .navbar-inner-nav a.active {
    color: var(--color-radiant);
    font-weight: 600;
  }

  .navbar-inner-nav a.active::after {
    background-color: var(--color-radiant);
    border-radius: 5px 5px 0 0;
    bottom: 0;
    content: '';
    height: 5px;
    left: 0;
    position: absolute;
    width: 100%;
  }

  .navbar-inner-nav a:hover {
    color: var(--color-radiant);
  }

  .navbar-inner-social {
    display: flex;
    gap: 24px;
    justify-content: space-between;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    .navbar-inner-social {
      display: none;
    }
  }

  .navbar-inner-social .navbar-inner-social-icon {
    align-items: center;
    background-color: var(--color-radiant);
    display: flex;
    height: 40px;
    justify-content: center;
    transition: var(--transition-duration-default);
    width: 40px;
  }

  .navbar-inner-social-icon img,
  svg {
    filter: invert(14%) sepia(5%) saturate(6344%) hue-rotate(187deg)
      brightness(87%) contrast(89%);
    height: 22px;
    width: 22px;
  }

  .navbar-inner-social-icon:hover {
    filter: brightness(0.8) saturate(1.25);
  }
}

@media (min-width: 280px) and (max-width: 560px) {
  .l-header {
    height: 100%;
  }
}
