.l-main {
  &__call_to_contact {
    background-color: var(--color-pico-void);
    height: 160px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__call_to_contact {
      height: 100%;
    }
  }

  &__call_to_contact .inner {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 1120px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__call_to_contact .inner {
      display: flex;
      flex-direction: column;
      padding: 32px 16px;
    }
  }

  &__call_to_contact .inner h4 {
    color: var(--color-white);
    font-family: Poppins, sans-serif;
    font-size: 25px;
    margin: 0;
    padding: 0;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__call_to_contact .inner h4 {
      font-size: 18px;
      text-align: center;
    }
  }

  &__call_to_contact .inner a {
    align-items: center;
    background-color: var(--color-light-gray);
    color: var(--color-pico-void);
    display: flex;
    font-size: 18px;
    font-weight: 600;
    height: 60px;
    justify-content: center;
    text-decoration: none;
    transition: var(--transition-duration-default);
    width: 265px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__call_to_contact .inner a {
      height: 40px;
      margin: 32px 0 16px auto;
      width: 225px;
    }
  }

  &__call_to_contact .inner a:hover {
    background-color: var(--color-radiant);
  }
}
