.l-main {
  position: relative;
  z-index: 101;
}

.swiper {
  --swiper-navigation-color: var(--color-radiant);
  --swiper-pagination-color: var(--color-radiant);

  height: 600px;
  position: relative;
  width: 100%;
}

@media (min-width: 280px) and (max-width: 560px) {
  .swiper {
    height: 100%;
  }
}

.swiper-wrapper h2 {
  display: none;
  margin: 0;
  padding: 0;
  visibility: hidden;
}

@media (min-width: 280px) and (max-width: 560px) {
  .swiper-slide {
    background-position: center;
    background-size: cover;
  }
}

.swiper-slide .swiper-slide-inner {
  align-items: center;
  display: flex;
  height: 100%;
  margin: 0 auto;
  max-width: var(--container);
}

@media (min-width: 280px) and (max-width: 560px) {
  .swiper-slide .swiper-slide-inner {
    align-items: center;
    justify-content: center;
    padding: 30px 0;
  }
}

.swiper-slide .swiper-slide-inner h3 {
  color: var(--color-white);
  font-size: 70px;
  line-height: 90px;
  margin: 0 0 30px;
  padding: 0;
}

@media (min-width: 280px) and (max-width: 560px) {
  .swiper-slide .swiper-slide-inner h3 {
    font-size: 30;
    line-height: 40px;
    margin: 0 0 20px auto;
    padding: 0;
    text-align: center;
  }
}

.swiper-slide .swiper-slide-inner a {
  align-items: center;
  background-color: var(--color-light-gray);
  color: var(--color-pico-void);
  display: flex;
  font-size: 18px;
  font-weight: 600;
  height: 60px;
  justify-content: center;
  text-decoration: none;
  transition: var(--transition-duration-default);
  width: 265px;
}

@media (min-width: 280px) and (max-width: 560px) {
  .swiper-slide .swiper-slide-inner a {
    height: 40px;
    width: 225px;
  }
}

.swiper-slide .swiper-slide-inner a:hover {
  background-color: var(--color-radiant);
}
