.l-main {
  &__services {
    background-color: var(--color-light-gray);
    padding: 100px 0;
    width: 100%;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__services {
      padding: 50px 0;
    }
  }

  &__inner {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: var(--container);
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__inner {
      display: flex;
      flex-direction: column;
      padding: 0 16px;
    }
  }

  &__title span {
    color: var(--color-gray);
    font-size: 16px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__title span {
      align-items: center;
      display: flex;
      justify-content: center;
      text-align: center;
    }
  }

  &__title h2 {
    color: var(--pico-void);
    font-size: 60px;
    position: relative;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__title h2 {
      font-size: 24px;
      text-align: center;
    }
  }

  &__title h2::after {
    border-bottom: 5px solid var(--color-radiant);
    content: '';
    display: block;
    margin-top: 20px;
    width: 75px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__title h2::after {
      margin: 16px auto;
    }
  }

  &__title a {
    align-items: center;
    background-color: var(--color-radiant);
    color: var(--color-pico-void);
    display: flex;
    font-size: 18px;
    font-weight: 600;
    height: 60px;
    justify-content: center;
    text-decoration: none;
    transition: var(--transition-duration-default);
    width: 265px;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__title a {
      height: 40px;
      margin: 30px auto;
      width: 225px;
    }
  }

  &__title a:hover {
    background-color: var(--color-pico-void);
    color: var(--color-white);
  }

  &__items-wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__items {
    display: grid;
    gap: 20px;
    grid-template-columns: 265px 265px;
  }

  &__items-3 {
    display: grid;
    gap: 20px;
    margin-top: 20px;
    grid-template-columns: 1fr 1fr 1fr;
  }

  @media (min-width: 280px) and (max-width: 560px) {
    &__items {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &__items-3 {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
  }

  &__items .item {
    align-items: center;
    border: 5px solid var(--color-radiant);
    color: var(--color-pico-void);
    display: flex;
    flex-direction: column;
    height: 225px;
    justify-content: center;
    text-decoration: none;
  }


  &__items .item h3 {
    font-size: 20px;
    font-weight: 900;
    margin: 0;
    padding: 0;
    text-align: center;
  }

  &__items-3 .item {
    align-items: center;
    border: 5px solid var(--color-radiant);
    color: var(--color-pico-void);
    display: flex;
    flex-direction: column;
    height: 175px;
    justify-content: center;
    text-decoration: none;
  }

  &__items-3 .item h3 {
    font-size: 18px;
    font-weight: 900;
    margin: 0;
    padding: 0;
    text-align: center;
  }
}
