.l-footer {
  background-color: #151c2f;
  padding: 50px 0;
}

.l-footer .inner {
  display: grid;
  gap: 20px;
  grid-template-columns: 265px 265px 265px 265px;
  margin: 0 auto;
  max-width: 1120px;
}

.l-footer .inner p {
  color: var(--color-white);
  font-size: 18px;
  margin-bottom: 0;
  margin-top: 18px;
}

.l-footer .inner span {
  color: var(--color-light-gray);
  font-weight: 600;
}

.l-footer .footer-social {
  display: grid;
  gap: 24px;
  grid-template-columns: 40px 40px 40px;
  justify-content: flex-end;
  margin-top: 0;
}

.l-footer .footer-social .footer-social-icon {
  align-items: center;
  background-color: var(--color-radiant);
  display: flex;
  height: 40px;
  justify-content: center;
  transition: var(--transition-duration-default);
  width: 40px;
}

.footer-social-icon > .icon {
  filter: invert(14%) sepia(5%) saturate(6344%) hue-rotate(187deg)
    brightness(87%) contrast(89%);
  height: 22px;
  width: 22px;
}

.l-footer .footer-links {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
}

.l-footer .footer-links h3 {
  align-items: center;
  color: var(--color-white);
  font-size: 20px;
  margin: 0 0 20px;
  padding: 0;
}

.l-footer .footer-links ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.footer-links ul li {
  font-size: 14px;
  margin-bottom: 10px;
}

.footer-links li a {
  color: var(--color-white);
  margin-bottom: 10px;
  text-decoration: none;
}
